/* Body styling to center the app */
body {
  font-family: Arial, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
  background-color: #f0f2f5;
}

/* Container for the Counter */
.counter-container {
  text-align: center;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 300px;
}

/* Counter value display styling */
.counter-value {
  font-size: 48px;
  color: #4caf50;
  margin: 20px 0;
}

/* Button styling */
.button {
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  margin: 5px;
  transition: background-color 0.3s ease;
}

/* Hover effect for buttons */
.button:hover {
  background-color: #45a049;
}

/* Reset button styling */
.reset-button {
  background-color: #f44336;
}

.reset-button:hover {
  background-color: #d32f2f;
}
